import { ApiProperty } from '@nestjs/swagger';

import { IPersonnelResponse } from '../../application/interface';

export class PersonnelResponse implements IPersonnelResponse {
  @ApiProperty({ title: 'Personnel id' })
  id: string;
  @ApiProperty({ title: 'Personnel name' })
  name: string;
  @ApiProperty({ title: 'Personnel phone number' })
  phone_number: string;
  @ApiProperty({ title: 'Personnel email' })
  email: string;
  @ApiProperty({ title: 'Personnel date of birth' })
  date_of_birth: string;
  @ApiProperty({ title: 'Personnel image', required: false })
  image_url?: string;
  @ApiProperty({ title: 'Personnel user type' })
  user_type: string;
}
